.page-serving {
    .wrapper {
        position: relative;
        left: 50%;
        transform: translateX(-50%);
        max-width: 1365px;
        padding: 0 30px;
    }

    .container {
        .wine-navigation {
            width: 100%;
            position: relative;
            display: flex;
            justify-content: space-between;
            padding: 40px 0;

            a {
                font-family: futura-pt,sans-serif;
                font-weight: 700;
                font-style: normal;
                font-size: 16px;
                color: $black;
                text-transform: uppercase;

            }

            .nav-left {
                a {
                    position: relative;
                    display: flex;
                    align-items: center;

                    &:hover {
                        &::after {
                            transform: scale(1.3);
                            transform-origin: center;
                        }
                    }

                    &.menu-icon-black {
                        &::after {
                            background: url(../../../assets/images/menu-icon-black.svg);
                        }
                    }
                    &.menu-icon-red {
                        &::after {
                            background: url(../../../assets/images/menu-icon-red.svg);
                        }
                    }
                    &.menu-icon-green {
                        &::after {
                            background: url(../../../assets/images/menu-icon-green.svg);
                        }
                    }
                    &.menu-icon-pink {
                        &::after {
                            background: url(../../../assets/images/menu-icon-pink.svg);
                        }
                    }
                    &.menu-icon-orange {
                        &::after {
                            background: url(../../../assets/images/menu-icon-orange.svg);
                        }
                    }

                    &::before {
                        content: '';
                        position: relative;
                        width: 28px;
                        height: 15px;
                        display: inline-block;
                        margin-right: 10px;
                        background: url(../../../assets/images/arrow-left.svg);
                        background-size: cover;
                        background-repeat: no-repeat;
                        background-position: center;
                    }


                    &::after {
                        content: '';
                        position: relative;
                        width: 20px;
                        height: 20px;
                        display: inline-block;
                        margin-left: 5px;
                        background-size: cover !important;
                        background-repeat: no-repeat !important;
                        background-position: center;
                        transition: .25s ease-in-out;
                    }
                }
            }

            .nav-right {
                a {
                    position: relative;
                    display: flex;
                    align-items: center;

                    &:hover {
                        &::before {
                            transform: scale(1.3);
                            transform-origin: center;
                        }
                    }

                    &.menu-icon-black {
                        &::before {
                            background: url(../../../assets/images/menu-icon-black.svg);
                        }
                    }
                    &.menu-icon-red {
                        &::before {
                            background: url(../../../assets/images/menu-icon-red.svg);
                        }
                    }
                    &.menu-icon-green {
                        &::before {
                            background: url(../../../assets/images/menu-icon-green.svg);
                        }
                    }
                    &.menu-icon-pink {
                        &::before {
                            background: url(../../../assets/images/menu-icon-pink.svg);
                        }
                    }
                    &.menu-icon-orange {
                        &::before {
                            background: url(../../../assets/images/menu-icon-orange.svg);
                        }
                    }

                    &::after {
                        content: '';
                        position: relative;
                        width: 28px;
                        height: 15px;
                        display: inline-block;
                        margin-left: 10px;
                        background: url(../../../assets/images/arrow-right.svg);
                        background-size: cover;
                        background-repeat: no-repeat;
                        background-position: center;
                    }


                    &::before {
                        content: '';
                        position: relative;
                        width: 20px;
                        height: 20px;
                        display: inline-block;
                        margin-right: 5px;
                        background-size: cover !important;
                        background-repeat: no-repeat !important;
                        background-position: center;
                        transition: .25s ease-in-out;
                    }
                }
            }
        }
        .intro-description {
            width: 100%;
            padding: 40px 0;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            text-align: center;
            
            .heading-4 {
                display: block;
                margin-top: 20px;
            }

            .nav-middle {
                margin-top: 20px;
                display: flex;
                justify-content: center;
                flex-wrap: wrap;
                a {
                    position: relative;
                    display: inline-block;
                    margin: 0 10px 10px;
                    font-family: futura-pt,sans-serif;
                    font-weight: 700;
                    font-style: normal;
                    font-size: 16px;
                    color: $black;
                    text-transform: uppercase;

                    @include mq(medium, 'max') {
                        font-size: 13px;
                    }

                    &::after {
                        content: '';
                        position: absolute;
                        bottom: 0;
                        left: 0;
                        width: 0;
                        height: 4px;
                        background: $black;
                        display: block;
                        transition: .25s ease-in-out
                    }

                    &:hover {
                        &::after {
                            width: 100%;
                          }
                      }
                }
            }
        }

        .content-target {
            position: relative;
            top: -100px;
        }

        .content-blocks-container {
            width: 100%;
            padding-bottom: 40px;

            .content-block {
                width: 100%;
                display: flex;
                flex-wrap: wrap;
                border: 4px solid $black;
                margin-bottom: 40px;

                .image {
                    width: 40%;
                    border-right: 4px solid $black;
                    text-align: center;
                    padding: 50px;

                    @include mq(large, 'max') {
                        width: 100%;
                        border-right: none;
                        border-bottom: 4px solid $black;
                    }
                }   

                .content-wrap {
                    width: 60%;

                    @include mq(large, 'max') {
                        width: 100%;
                    }

                    .description-title {
                        border-bottom: 4px solid $black;
                        padding: 20px;

                        @include mq(large, 'max') {
                            text-align: center;
                        }
                    }

                    .content-row {
                        padding: 20px 30px 0px 30px;

                        &:last-of-type {
                            padding-bottom: 40px
                        }
                    }
                }
            }
        }
    }
}